import React, {useState} from 'react'
import {Dropdown, Layout, Menu, Modal, Form, Input, Button} from 'antd'
import {
  DownOutlined,
  MenuFoldOutlined,
  MenuUnfoldOutlined,
} from '@ant-design/icons'
import {useStores} from '../../stores'
import {observer} from 'mobx-react-lite'
import {changeAdminUser} from '../../apis/admin_user'

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16},
}
const tailLayout = {
  wrapperCol: {offset: 8, span: 16},
}

const BasicHeader = () => {
  const {appStore} = useStores()
  const [isModalVisible, setIsModalVisible] = useState(false)

  const showModal = () => {
    setIsModalVisible(true)
  }

  const onFinish = (values: any) => {
    const {password: pwd} = values
    changeAdminUser({pwd, id: appStore.userInfo.id}).then(() => {
      alert('비밀번호 수정이 성공하였습니다.')
      setIsModalVisible(false)
    })
  }

  const {isCollapsed, logout, userInfo} = appStore
  return (
    <Layout.Header
      style={{display: 'flex', background: '#fff', padding: '0 20px'}}
    >
      <div style={{flex: 1}}>
        {isCollapsed ? (
          <MenuFoldOutlined onClick={() => appStore.setIsCollapsed(false)} />
        ) : (
          <MenuUnfoldOutlined onClick={() => appStore.setIsCollapsed(true)} />
        )}
      </div>
      <div>
        <Dropdown
          trigger={['click']}
          overlay={
            <Menu>
              <Menu.Item onClick={showModal}>
                <span>비밀번호 변경</span>
              </Menu.Item>
              <Menu.Item
                onClick={() => {
                  logout()
                  window.location.href = '/dragonraja-admin/#/login'
                }}
              >
                <span>Sign Out</span>
              </Menu.Item>
            </Menu>
          }
        >
          <a className="ant-dropdown-link" onClick={(e) => e.preventDefault()}>
            {userInfo.id} <DownOutlined />
          </a>
        </Dropdown>
      </div>
      <Modal title="비밀번호 변경" visible={isModalVisible} footer={null}>
        <Form {...layout} onFinish={onFinish}>
          <Form.Item
            label="Password"
            name="password"
            rules={[{required: true, message: '비밀번호를 입력해주세요.'}]}
            hasFeedback
          >
            <Input.Password />
          </Form.Item>
          <Form.Item
            name="confirm"
            label="Confirm Password"
            dependencies={['password']}
            hasFeedback
            rules={[
              {
                required: true,
                message: '비밀번호를 다시 입력해주세요.',
              },
              ({getFieldValue}) => ({
                validator(_, value) {
                  if (!value || getFieldValue('password') === value) {
                    return Promise.resolve()
                  }
                  return Promise.reject(
                    new Error('비밀번호가 일치하지 않습니다!')
                  )
                },
              }),
            ]}
          >
            <Input.Password />
          </Form.Item>
          <Form.Item {...tailLayout}>
            <Button type="primary" htmlType="submit">
              변경하기
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </Layout.Header>
  )
}

export default observer(BasicHeader)
