import React, {useState} from 'react'
import {Table, Tabs} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {observer} from 'mobx-react-lite'

export const ItemType = [
  {label: '상자', value: 1},
  {label: '보석', value: 4},
  {label: '부스터', value: 5},
  {label: '티켓', value: 6},
  {label: '재료 아이템(일반)', value: 7},
  {label: '무기조각', value: 8},
]

const Items: React.FC<any> = ({items = [], size}) => {
  const [activeKey, setActiveKey] = useState(ItemType[0].label)
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '아이템 이름', dataIndex: 'label'},
    {title: '보유 수량', dataIndex: 'amount'},
    {title: '비고', dataIndex: 'etc'},
  ]

  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}} size={size}>
        <Tabs.TabPane tab="보유 아이템" key="보유 아이템">
          <BasicTabs
            type={'line'}
            tabs={ItemType.map((type) => type.label)}
            style={{marginTop: 0}}
            size={size}
            onChange={(activeKey: string) => setActiveKey(activeKey)}
          />
          <div>
            <Table
              size={size}
              columns={columns}
              dataSource={items
                .filter(
                  (item: any) =>
                    item.type ===
                    (
                      ItemType.find((type) => type.label === activeKey) || {
                        value: 1,
                      }
                    ).value
                )
                .map((item: any, idx: number) => ({
                  ...item,
                  number: idx + 1,
                }))}
            />
          </div>
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default observer(Items)
