import React, {useState} from 'react'
import {Button, DatePicker, Form, Input, Select, Space, Checkbox} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {DownOutlined, UpOutlined} from '@ant-design/icons'
import RewardItem from './RewardItem'
import StyledButton from '../Button/StyledButton'
import {sendMail} from '../../apis/player'
import {observer} from 'mobx-react-lite'
import {useStores} from '../../stores'
import {Tiers} from '../player/Equipments'
import {ResourceType} from '../player/Resources'

const RewardTiers = [...Tiers, '세트']

const MailForm: React.FC<{players: any; onClose: any}> = ({
  players = [],
  onClose = () => {},
}) => {
  const [visibleReward, setVisibleReward] = useState(true)
  const [isAllPlayers, setIsAllPlayers] = useState(false)
  const [reward, setReward] = useState<any>({equipmentKeyList: []})
  const {
    appStore: {
      gameItemData,
      equipmentKeyList,
      server: {cid},
    },
  } = useStores()
  const send = (value: any) => {
    let data: any = {pids: players.map((p: any) => p.pid).join(',')}

    if (isAllPlayers) {
      data = {cid}
    }
    return sendMail({
      ...data,
      content: {...value, ...reward},
    }).then(() => {
      alert('우편 보내기 완료')
      onClose()
    })
  }

  return (
    <BasicTabs tabs={['일반 우편']} style={{marginTop: 10}}>
      <Form
        requiredMark={false}
        colon={false}
        labelCol={{xs: 5}}
        wrapperCol={{xs: 19}}
        onFinish={send}
      >
        <Form.Item
          label={'제목'}
          name={'title'}
          style={{marginBottom: 10}}
          rules={[{required: true, message: '제목을 입력해주세요.'}]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={'대상'} style={{marginBottom: 10}}>
          <div style={{color: '#40A3FF'}}>
            {isAllPlayers
              ? '전체 플레이어'
              : `선택된 플레이어 ${players.length}명`}
          </div>
          <div>
            <Checkbox
              checked={isAllPlayers}
              onChange={(e) => setIsAllPlayers(e.target.checked)}
            >
              전체 플레이어
            </Checkbox>
          </div>
        </Form.Item>
        <Form.Item
          label={'시간'}
          name={'time'}
          style={{marginBottom: 10}}
          rules={[{required: true, message: '시간을 입력해주세요.'}]}
        >
          <DatePicker style={{width: 150}} />
        </Form.Item>
        <Form.Item
          label={'서비스 국가'}
          name={'country'}
          style={{marginBottom: 10}}
        >
          <Select style={{width: 150}}>
            <Select.Option value="korea">전체/한국</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label={'언어'} name={'lang'} style={{marginBottom: 30}}>
          <Select style={{width: 150}}>
            <Select.Option value="korean">한국어</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label={'상세 내용'} name={'body'} style={{marginBottom: 30}}>
          <Input.TextArea />
        </Form.Item>
        <Form.Item wrapperCol={{offset: 5, span: 19}} style={{marginBottom: 0}}>
          <>
            <Button
              style={{borderRadius: 5, marginRight: 10}}
              size={'small'}
              onClick={() => setVisibleReward(!visibleReward)}
            >
              {visibleReward ? <DownOutlined /> : <UpOutlined />}
            </Button>
            보상 제외
          </>
        </Form.Item>
        {visibleReward && (
          <Form.Item label={'보상 내용'} style={{marginBottom: 15}}>
            <RewardItem
              productOptions={Object.keys(gameItemData).map(
                (key) => gameItemData[key]
              )}
              placeholder={'게임아이템'}
              onChange={(p: any, v: any) => {
                if (v > 0) {
                  const newReward = {...reward}
                  newReward.gameItemReward = {
                    ...newReward.gameItemReward,
                    [p]: v,
                  }
                  setReward(newReward)
                }
              }}
            />
            <RewardItem
              noCount
              productOptions={Object.keys(equipmentKeyList).map((key) => {
                return {
                  ...equipmentKeyList[key],
                  label: `${equipmentKeyList[key].label}(${
                    RewardTiers[equipmentKeyList[key].tier - 1]
                  })`,
                }
              })}
              placeholder={'장비'}
              onChange={(p: any) => {
                const newReward = {...reward}
                newReward.equipmentKeyList = [p]
                setReward(newReward)
              }}
            />
            <RewardItem
              productOptions={ResourceType.map((r) => ({...r, label: r.name}))}
              placeholder={'재화'}
              onChange={(p: any, v: any) => {
                if (v > 0) {
                  const newReward = {...reward}
                  newReward.userResourceReward = {
                    ...newReward.userResourceReward,
                    [p]: v,
                  }
                  setReward(newReward)
                }
              }}
            />
          </Form.Item>
        )}
        <div
          style={{
            borderTop: '1px solid #ccc',
            paddingTop: 10,
            marginTop: 30,
            textAlign: 'right',
          }}
        >
          <Space>
            <StyledButton purple type={'submit'}>
              우편 보내기
            </StyledButton>
            <StyledButton type={'button'} gray onClick={onClose}>
              닫기
            </StyledButton>
          </Space>
        </div>
      </Form>
    </BasicTabs>
  )
}

export default observer(MailForm)
